.movie-section {
  /*overflow: hidden;*/ }
  .movie-section__background {
    /*height: 470px;*/
    height: 400px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 50% 0%;
    /*filter: blur(2px);*/
    /*transform: scale(1.1);*/ }
  .movie-section__bg-effect {
    background-color: rgba(0, 0, 1, 0.2);
    width: 100%;
    height: 100%; }
  .movie-section__content {
    position: relative;
    background-color: #1d1d1d;
    color: #fff;
    padding: 50px 0; }
  .movie-section__info {
    margin: 0 auto;
    margin-top: 150px;
    width: 90%;
    text-align: center;
    /*@media only screen and (min-width: 978px) {*/ }
    @media only screen and (min-width: 61.125em) {
      .movie-section__info {
        /*@media (min-width: 800px) {*/
        margin: 0;
        margin-left: 465px;
        width: 50%;
        text-align: left; } }
  .movie-section__poster {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    position: absolute;
    bottom: 190px;
    left: 50%;
    width: 255px;
    height: 400px;
    z-index: 5;
    transform: translate(-50%, -50%);
    /*@media only screen and (min-width: 768px) {*/
    /*@media only screen and (min-width: 978px) {*/ }
    @media only screen and (min-width: 48em) {
      .movie-section__poster {
        bottom: 150px; } }
    @media only screen and (min-width: 61.125em) {
      .movie-section__poster {
        /*@media (min-width: 800px) {*/
        transform: translate(0, 0);
        bottom: 70px;
        left: 130px;
        width: 280px;
        height: 430px; } }

.movieVideo {
  display: block;
  width: 90%;
  max-width: 594px;
  height: 340px;
  margin: 0 auto;
  margin-top: 100px;
  border: none;
  /*@media only screen and (min-width: 978px) {*/ }
  @media only screen and (min-width: 61.125em) {
    .movieVideo {
      /*@media (min-width: 800px) {*/
      max-width: 864px;
      height: 498px; } }

/******** Movie Video Image ***********/
.movieVideoImage {
  width: 90%;
  max-width: 594px;
  height: 340px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  margin: 0 auto;
  margin-top: 100px;
  position: relative;
  overflow: hidden;
  /*@media only screen and (min-width: 978px) {*/ }
  @media only screen and (min-width: 61.125em) {
    .movieVideoImage {
      /*@media (min-width: 800px) {*/
      max-width: 864px;
      height: 498px; } }
  .movieVideoImage:hover {
    cursor: pointer; }
  .movieVideoImage:hover .movieVideoImage__icon-wrapper {
    transform: translate(-50%, -50%) scale(0.7);
    opacity: 1;
    border: 5px solid #0279f8; }
  .movieVideoImage:hover .movieVideoImage__controls {
    bottom: 0; }
  .movieVideoImage:hover .movieVideoImage__effect {
    background-color: rgba(0, 0, 0, 0.5); }
  .movieVideoImage__effect {
    transition: .2s;
    width: 100%;
    height: 100%; }
  .movieVideoImage__icon-wrapper {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    /*width: 80px;*/
    /*height: 80px;*/
    background-color: black;
    padding: 25px;
    border-radius: 100%;
    opacity: .7;
    transition: .2s; }
  .movieVideoImage__icon {
    position: relative;
    /*border: 1px solid red;*/
    /*padding: 40px;*/
    border-radius: 100%;
    width: 40px;
    height: 40px;
    background-color: white; }
  .movieVideoImage .faPlay {
    position: absolute;
    top: 50%;
    left: 56%;
    transform: translate(-50%, -50%);
    font-size: 20px; }
  .movieVideoImage__controls {
    position: absolute;
    bottom: -40px;
    transition: .2s;
    width: 100%;
    background-color: black;
    opacity: .6;
    color: white;
    padding: 5px 17px; }
  .movieVideoImage .col-6:last-child {
    text-align: right; }
  .movieVideoImage .faPlay-control,
  .movieVideoImage .faVolumeUp,
  .movieVideoImage .time {
    margin-right: 22px; }
  .movieVideoImage .faCog,
  .movieVideoImage .faArrowsAlt {
    margin-left: 22px; }
