.intro-section {
  width: 100%;
  height: 440px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 20%;
  position: relative; }
  .intro-section__bg-effect {
    background-color: rgba(0, 0, 1, 0.6);
    width: 100%;
    height: 100%; }
  .intro-section__details {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    width: 80%; }
  .intro-section__title {
    color: white;
    font-size: 30px;
    /*@media only screen and (min-width: 978px) {*/ }
    @media only screen and (min-width: 61.125em) {
      .intro-section__title {
        /*@media (min-width: 800px) {*/
        font-size: 40px; } }
  .intro-section__description {
    color: #fff;
    font-size: 22px; }
