footer {
  background-color: #1d1d1d;
  color: #eee; }

hr {
  width: 97%;
  margin: 0 auto !important;
  background-color: #2c2d2f; }

.facebook,
.twitter,
.instagram,
.google {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 40px;
  color: white;
  margin-right: 10px; }
  .facebook:hover,
  .twitter:hover,
  .instagram:hover,
  .google:hover {
    opacity: 0.7;
    color: white; }

.faFacebookF,
.faTwitter,
.faInstagram,
.faGooglePlusG {
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%; }

.facebook {
  background-color: #3b5998; }

.twitter {
  background-color: #55acee; }

.instagram {
  background-color: #125688; }

.google {
  background-color: #dd4b39; }

.footer-logo {
  width: 40%;
  margin: 0 auto;
  padding-left: 17px;
  padding-top: 17px;
  transform: scale(1.6);
  /*@media only screen and (min-width: 768px) {*/ }
  @media only screen and (min-width: 48em) {
    .footer-logo {
      margin: 0; } }
